// @flow
import RcfPlayer from '@rcf/universal-library-rcf-player';

import * as adapter from './adapter';

import { DISPLAY_TYPES, TITLEBAR_POSITIONS, THEMES } from '@rcf/universal-library-rcf-player';

import type { RcfPlayerOptions } from '@rcf/universal-library-rcf-player';

document.addEventListener('DOMContentLoaded', async () => {
	const urlParameters = new URLSearchParams(window.location.search);
	const playerActivitySetId = urlParameters.get('path'); // should be something like L0/U01/S01/AS02 etc
	const playlist = urlParameters.get('playlist') === 'y';
	const userRole = urlParameters.get('userRole') || 'student';
	const linkOrigin = urlParameters.get('linkOrigin') || 'hotspot';
	const isDesktop = (urlParameters.get('isDesktop') || 'y') === 'y';

	if (!playerActivitySetId && !playlist) {
		alert('No path or playlistIds provided to rcf-player!');
		return;
	}

	const rcfPlayer = getRcfPlayer();

	// the viewer backend will either use a 'real' activity set, or create a fake one based on the path provided
	const viewActivityOptions = {
		userRole,
		linkOrigin,
		student: 'mock-student-id',
		changedActivitySet: false,
		mobileModeOverride: isDesktop ? undefined : true
	};

	if (playerActivitySetId && !playlist) {
		viewActivityOptions.activitySetId = playerActivitySetId;
	} else {
		const response = await fetch(`api.php/playlist/${playerActivitySetId}`);
		const playlistIds = await response.json();

		viewActivityOptions.activitySetId = playlistIds[0];
		viewActivityOptions.playList = {
			type: 'activity-set-list',
			ids: playlistIds
		};
	}

	// show the player !
	rcfPlayer.viewActivity(viewActivityOptions);
});

const getRcfPlayer = () => {
	const urlParameters = new URLSearchParams(window.location.search);
	const isDesktop = (urlParameters.get('isDesktop') || 'y') === 'y';
	const runtimeLanguage = urlParameters.get('runtimeLanguage') || 'en';
	const theme = urlParameters.get('theme') || '';

	// the div to render the player into
	const rootDiv = document.getElementById('player-container');

	// the document + window for this player
	const contentWebviewDocument = document;
	const contentWebviewWindow = window;

	// initial display type is fullscreen, just like the scorm player
	const initialDisplayType = isDesktop ? DISPLAY_TYPES.windowed : DISPLAY_TYPES.fullScreen;

	// where the rcfPlayer css / fonts etc are kept
	const rcfPlayerAssetsUrl = 'compiled/rcfPlayer';

	// eg ... http://localhost:99/rcf-sandbox/rcf-source/viewer/rcf-player.html?path=L01/U01/AS01/etc
	//        http://localhost:99/rcf-source/viewer/rcf-player.html?path=L01/U01/AS01/etc
	//
	const contentRootUrl =
		window.location.origin + window.location.pathname.split('/').slice(0, -2).join('/');

	let titleBarPosition;

	switch (theme) {
		case THEMES.theme3:
			titleBarPosition = TITLEBAR_POSITIONS.bottom;
			break;

		default:
			titleBarPosition = TITLEBAR_POSITIONS.top;
	}

	const rcfPlayerOptions: RcfPlayerOptions = {
		language: runtimeLanguage,
		theme,
		titleBarPosition,
		contentRootUrl,
		contentWebviewDocument,
		contentWebviewWindow,
		initialDisplayType,
		rcfAdapter: adapter,
		debugMode: '',
		rcfPlayerAssetsUrl,
		rootDiv
	};

	return new RcfPlayer(rcfPlayerOptions);
};
