// @flow
import type {
	EventOptions,
	GetManifestForActivitySetOptions,
	ManifestDetails,
	GetAnswersForActivitySetOptions,
	AnswersByActivity,
	SetAnswersForActivitySetOptions,
	UploadUserFileDetails,
	UiDialogOptions,
	UiDialogResponse
} from '@rcf/universal-library-rcf-player';

// function to show a dialog to the user when there is an error or success message from the player
async function displayUiDialog(options: UiDialogOptions): Promise<UiDialogResponse> {
	const { text, buttons } = options;

	if (buttons) {
		await window.confirm(text);
	} else {
		await window.alert(text);
	}
}

async function getManifestForActivitySet(
	activitySetOptions: GetManifestForActivitySetOptions
): Promise<ManifestDetails> {
	// activity set id will be : L0/U01/AS01 etc
	const { activitySetId } = activitySetOptions;

	const response = await fetch(`api.php/manifest/activitySet/${activitySetId}`);
	const manifestJson = await response.json();

	console.log('checking if we should override the activityMode');
	const urlParameters = new URLSearchParams(window.location.search);
	const activityMode = urlParameters.get('activityMode') || '';

	if (activityMode.length > 0) {
		manifestJson.activitySets[activitySetId].activityMode = activityMode;
	}
	// log to the console
	console.log(
		'got the manifest for activity set',
		activitySetId,
		'\n',
		JSON.stringify(manifestJson, null, 2)
	);

	return manifestJson;
}

// async onEvent function taking EventOptions as argument
async function onEvent(eventOptions: EventOptions): Promise<void> {
	console.log('received event from player : ', eventOptions);
	switch (eventOptions.type) {
		case 'close-window-requested':
			window.close();
			return;

		case 'playlist-completion-requested':
			window.close();
			return;
	}
}

// async getAnswersForActivitySet function taking GetAnswersForActivitySetOptions as argument
async function getAnswersForActivitySet(
	activitySetOptions: GetAnswersForActivitySetOptions
): Promise<AnswersByActivity> {
	// get the results from the saved sessionAnswers
	const { activitySetId } = activitySetOptions;
	const lmsKey = `${window.location.pathname}/path=${activitySetId}`;
	const results = window.localStorage.getItem(lmsKey);

	if (results?.length > 0) {
		const activitySetOptions = JSON.parse(results);
		return activitySetOptions.answers || null;
	} else {
		return null;
	}
}

// async setAnswersForActivitySet function taking SetAnswersForActivitySetOptions as argument
async function setAnswersForActivitySet(
	activitySetOptions: SetAnswersForActivitySetOptions
): Promise<void> {
	// activity set id will be : L0/U01/AS01 etc
	// - need to convert the '/' into '|' for ... reasons ...
	const { activitySetId } = activitySetOptions;
	const $ = window.jQuery;

	// store activitySetOptions in sessionAnswers by activitySetId
	const lmsKey = `${window.location.pathname}/path=${activitySetId}`; // eg. /rcf-source-monorepo/viewer/rcf-player.html/path=L0/U01/AS01

	// get the title from the rubtic if possible
	if (!!$) {
		const activityTitle = $('.rubric').text();
		activitySetOptions.activityTitle = activityTitle;
	}
	window.localStorage.setItem(lmsKey, JSON.stringify(activitySetOptions));
	if (window.opener) {
		window.opener.postMessage('answers-saved', '*');
	}
}

// async function uploadUserFile to return fulfilled promise
async function uploadUserFile(
	fileDetails: UploadUserFileDetails,
	isTeacherRecording: boolean
): Promise<string> {
	await console.log(
		'uploadUserFile called with : fileDetails',
		fileDetails,
		'isTeacherRecording',
		isTeacherRecording
	);
	if (isTeacherRecording) {
		return './audio/teacher-recording.mp3';
	} else {
		return './audio/student-recording.mp3';
	}
}

export {
	getManifestForActivitySet,
	onEvent,
	getAnswersForActivitySet,
	displayUiDialog,
	setAnswersForActivitySet,
	uploadUserFile
};
